@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;500;600;700&family=Inter:wght@300;400;500;600;700;800;900&family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

/* CSS Variables */
:root {
  --primary-color: #e69500; /* slightly darker orange */
  --primary-dark: #b37400; /* even darker orange */
  --secondary-color: #ffae1a; /* light but deeper orange */
  --accent-color: #06b6d4;
  --background-color: #0a0a0f;
  --surface-color: #1a1a2e;
  --surface-light: #252545;
  --text-primary: #ffffff;
  --text-secondary: #a1a1aa;
  --text-muted: #71717a;
  --border-color: #27272a;
  --border-light: #3f3f46;
  --success-color: #22c55e;
  --warning-color: #f59e0b;
  --error-color: #ef4444;
  --shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --gradient-primary: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  --gradient-accent: linear-gradient(135deg, var(--accent-color), var(--primary-color));
  --font-primary: 'Inter', sans-serif;
  --font-mono: 'JetBrains Mono', monospace;
  --font-display: 'Space Grotesk', sans-serif;
}

/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-primary);
  background-color: var(--background-color);
  color: var(--text-primary);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

/* Beta Banner */
.beta-banner {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  padding: 12px 0;
  position: static;
  z-index: 1001;
  overflow: hidden;
}

.beta-banner::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  animation: shimmer 3s infinite;
}

@keyframes shimmer {
  0% { left: -100%; }
  100% { left: 100%; }
}

.beta-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  flex-wrap: wrap;
  text-align: center;
}

.beta-icon {
  width: 20px;
  height: 20px;
  color: white;
  animation: rotate 4s linear infinite;
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.beta-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  text-align: center;
}

.beta-label {
  font-family: var(--font-mono);
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.1em;
  opacity: 0.9;
}

.beta-message {
  font-size: 14px;
  font-weight: 500;
  max-width: 400px;
}

.beta-join {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 50px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.beta-join:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-1px);
}

.beta-join svg {
  width: 16px;
  height: 16px;
}

.beta-close {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.beta-close:hover {
  background: rgba(255, 255, 255, 0.2);
}

.beta-close svg {
  width: 16px;
  height: 16px;
}

/* Header */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(10, 10, 15, 0.8);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
  margin-top: 0;
}

.header.scrolled {
  background: rgba(10, 10, 15, 0.95);
  border-bottom-color: rgba(139, 92, 246, 0.3);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
  padding: 16px 0;
}

/* Logo */
.logo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-icon {
  width: 40px;
  height: 40px;
  background: var(--gradient-primary);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.logo-icon svg {
  width: 20px;
  height: 20px;
}

.logo-text {
  display: flex;
  flex-direction: column;
  line-height: 1;
}

.logo-main {
  font-family: var(--font-display);
  font-size: 16px;
  font-weight: 700;
  color: var(--text-primary);
  letter-spacing: 0.5px;
}

.logo-sub {
  font-family: var(--font-mono);
  font-size: 10px;
  font-weight: 500;
  color: var(--text-secondary);
  letter-spacing: 0.5px;
}

/* Navigation */
.nav {
  display: flex;
  align-items: center;
  gap: 32px;
}

.nav-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  transition: color 0.3s ease;
  position: relative;
  padding: 8px 0;
  letter-spacing: 0.3px;
}

.nav-link:hover,
.nav-link.active {
  color: var(--text-primary);
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--gradient-primary);
  transition: width 0.3s ease;
}

.nav-link:hover::after,
.nav-link.active::after {
  width: 100%;
}

/* Header Actions */
.header-actions {
  display: flex;
  align-items: center;
  gap: 16px;
}

.mobile-menu-toggle {
  display: none;
  flex-direction: column;
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  gap: 4px;
}

.mobile-menu-toggle span {
  width: 20px;
  height: 2px;
  background: var(--text-primary);
  transition: all 0.3s ease;
}

/* Mobile Menu */
.mobile-menu {
  display: none;
  padding: 24px 0;
  border-top: 1px solid var(--border-color);
}

.mobile-nav {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 24px;
}

.mobile-actions {
  display: flex;
  gap: 12px;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  letter-spacing: 0.3px;
}

.btn svg {
  width: 16px;
  height: 16px;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(139, 92, 246, 0.3);
}

.btn-outline {
  background: transparent;
  color: var(--text-primary);
  border: 1px solid var(--border-light);
}

.btn-outline:hover {
  background: var(--surface-light);
  border-color: var(--primary-color);
}

.btn-large {
  padding: 16px 32px;
  font-size: 16px;
}

/* Hero Section */
.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.hero-bg {
  position: absolute;
  inset: 0;
  z-index: 1;
}

.grid-pattern {
  position: absolute;
  inset: 0;
  background-image: 
    linear-gradient(rgba(139, 92, 246, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(139, 92, 246, 0.1) 1px, transparent 1px);
  background-size: 50px 50px;
  animation: grid-move 20s linear infinite;
}

@keyframes grid-move {
  0% { transform: translate(0, 0); }
  100% { transform: translate(50px, 50px); }
}

.gradient-overlay {
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 50% 50%, rgba(230, 149, 0, 0.15) 0%, transparent 70%);
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  padding: 160px 0 80px;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(139, 92, 246, 0.1);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: 50px;
  font-size: 14px;
  font-weight: 500;
  color: var(--primary-color);
  margin-bottom: 32px;
  letter-spacing: 0.5px;
}

.hero-badge svg {
  width: 16px;
  height: 16px;
}

.hero-title {
  font-family: var(--font-display);
  font-size: clamp(3rem, 8vw, 6rem);
  font-weight: 700;
  line-height: 1.1;
  margin-bottom: 24px;
  letter-spacing: -0.02em;
  text-align: center;
}

.title-line {
  display: block;
}

.subtitle {
  font-family: var(--font-mono);
  font-size: clamp(1.5rem, 4vw, 2.5rem);
  font-weight: 500;
  letter-spacing: 0.1em;
  margin-top: 8px;
}

.gradient-text {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-description {
  font-size: 20px;
  color: var(--text-secondary);
  margin-bottom: 40px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
  text-align: center;
}

.hero-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
  margin-bottom: 60px;
  flex-wrap: wrap;
}

.hero-stats {
  display: flex;
  gap: 48px;
  justify-content: center;
  flex-wrap: wrap;
}

.stat-item {
  text-align: center;
}

.stat-value {
  font-family: var(--font-display);
  font-size: 32px;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 4px;
}

.stat-label {
  font-size: 14px;
  color: var(--text-secondary);
  letter-spacing: 0.3px;
}

/* Stats Section */
.stats {
  padding: 80px 0;
  background: var(--surface-color);
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 32px;
}

.stat-card {
  text-align: center;
  padding: 40px 32px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  transition: all 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-8px);
  border-color: var(--primary-color);
  box-shadow: 0 20px 40px rgba(139, 92, 246, 0.1);
}

.stat-card .stat-icon {
  width: 48px;
  height: 48px;
  margin: 0 auto 16px;
  color: var(--primary-color);
}

.stat-card .stat-icon svg {
  width: 100%;
  height: 100%;
}

.stat-card .stat-number {
  font-family: var(--font-display);
  font-size: 48px;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 8px;
}

.stat-card .stat-label {
  font-size: 16px;
  color: var(--text-secondary);
  letter-spacing: 0.3px;
}

/* Section Styles */
.section-header {
  text-align: center;
  margin-bottom: 80px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.section-title {
  font-family: var(--font-display);
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 16px;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-align: center;
}

.section-description {
  font-size: 20px;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
  text-align: center;
}

/* Features Section */
.features {
  padding: 120px 0;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 32px;
}

.features-grid > .feature-card:last-child:nth-child(odd) {
  grid-column: span 2;
}

.feature-card {
  padding: 40px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  transition: all 0.3s ease;
  text-align: left;
}

.feature-card:hover {
  transform: translateY(-8px);
  border-color: var(--primary-color);
  box-shadow: 0 20px 40px rgba(139, 92, 246, 0.1);
}

.feature-icon {
  width: 48px;
  height: 48px;
  color: var(--primary-color);
  margin-bottom: 24px;
}

.feature-icon svg {
  width: 100%;
  height: 100%;
}

.feature-title {
  font-family: var(--font-display);
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 16px;
  color: var(--text-primary);
}

.feature-description {
  color: var(--text-secondary);
  margin-bottom: 24px;
  line-height: 1.6;
}

.feature-list {
  list-style: none;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  color: var(--text-secondary);
}

.feature-item svg {
  width: 16px;
  height: 16px;
  color: var(--success-color);
  flex-shrink: 0;
}

/* Services Section */
.services {
  padding: 120px 0;
  background: var(--surface-color);
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 32px;
  align-items: start;
}

.service-card {
  position: relative;
  padding: 40px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  transition: all 0.3s ease;
  text-align: center;
}

.service-card:hover {
  transform: translateY(-8px);
  border-color: var(--primary-color);
  box-shadow: 0 20px 40px rgba(139, 92, 246, 0.1);
}

.service-card.popular {
  border-color: var(--primary-color);
  background: rgba(139, 92, 246, 0.1);
}

.popular-badge {
  position: absolute;
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 16px;
  background: var(--gradient-primary);
  color: white;
  font-size: 12px;
  font-weight: 600;
  border-radius: 50px;
  letter-spacing: 0.5px;
}

.popular-badge svg {
  width: 14px;
  height: 14px;
}

.service-header {
  text-align: center;
  margin-bottom: 32px;
}

.service-name {
  font-family: var(--font-display);
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 8px;
}

.service-price {
  margin-bottom: 16px;
}

.price {
  font-family: var(--font-display);
  font-size: 48px;
  font-weight: 700;
  color: var(--primary-color);
}

.period {
  font-size: 16px;
  color: var(--text-secondary);
  font-family: var(--font-mono);
}

.service-description {
  color: var(--text-secondary);
  line-height: 1.6;
}

.service-features {
  list-style: none;
  margin-bottom: 32px;
  text-align: left;
}

.service-feature {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
  color: var(--text-secondary);
}

.service-feature svg {
  width: 16px;
  height: 16px;
  color: var(--success-color);
  flex-shrink: 0;
}

.service-btn {
  width: 100%;
  justify-content: center;
}

/* Footer */
.footer {
  background: var(--background-color);
  border-top: 1px solid var(--border-color);
  padding: 80px 0 32px;
}

.footer-content {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 48px;
  margin-bottom: 48px;
}

.footer-section h4 {
  font-family: var(--font-display);
  color: var(--text-primary);
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 16px;
  letter-spacing: 0.3px;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.footer-description {
  color: var(--text-secondary);
  margin-bottom: 24px;
  line-height: 1.6;
}

.footer-links {
  list-style: none;
}

.footer-link {
  display: block;
  color: var(--text-secondary);
  text-decoration: none;
  margin-bottom: 8px;
  transition: color 0.3s ease;
}

.footer-link:hover {
  color: var(--primary-color);
}

.social-links {
  display: flex;
  gap: 16px;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
}

.social-link:hover {
  background: var(--primary-color);
  color: white;
}

.social-link svg {
  width: 20px;
  height: 20px;
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 32px;
  border-top: 1px solid var(--border-color);
  color: var(--text-secondary);
  font-size: 14px;
}

.footer-status {
  display: flex;
  align-items: center;
  gap: 8px;
}

.status-indicator {
  width: 8px;
  height: 8px;
  background: var(--success-color);
  border-radius: 50%;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Responsive Design */
@media (max-width: 768px) {
  .desktop-nav {
    display: none;
  }

  .mobile-menu-toggle {
    display: flex;
  }

  .mobile-menu.open {
    display: block;
  }

  .hero-content {
    padding: 140px 0 60px;
  }

  .hero-actions {
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
  }

  .hero-stats {
    gap: 32px;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .features {
    padding: 60px 0;
  }

  .feature-card {
    padding: 20px;
  }

  .feature-icon {
    width: 36px;
    height: 36px;
    margin-bottom: 16px;
  }

  .feature-title {
    font-size: 18px;
  }

  .feature-description {
    font-size: 15px;
    margin-bottom: 16px;
  }

  .feature-item {
    font-size: 14px;
    gap: 8px;
  }

  .features-grid {
    display: flex;
    flex-direction: column;
    gap: 16px;
    grid-template-columns: unset;
  }

  .feature-card {
    width: 100%;
    margin: 0;
  }

  .feature-title {
    font-size: 16px;
    margin-bottom: 10px;
  }

  .feature-description {
    font-size: 13px;
    margin-bottom: 10px;
  }

  .feature-item {
    font-size: 12px;
    gap: 6px;
    margin-bottom: 8px;
  }

  .services-grid {
    grid-template-columns: 1fr;
  }

  .footer-content {
    grid-template-columns: 1fr;
    gap: 32px;
  }

  .footer-bottom {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }

  .mobile-actions {
    flex-direction: column;
  }

  .beta-content {
    flex-direction: column;
    gap: 12px;
  }

  .section-title {
    font-size: 36px;
  }

  .hero-title {
    text-align: center;
  }

  .subtitle {
    text-align: left;
  }

  .section-header,
  .section-title,
  .section-description,
  .feature-card,
  .feature-title,
  .feature-description {
    text-align: left;
  }

  .hero-content,
  .hero-title,
  .hero-description,
  .hero-badge {
    text-align: left;
  }
}

/* App Loading State */
.App {
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.6s ease;
}

.App.loaded {
  opacity: 1;
  transform: translateY(0);
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--background-color);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-dark);
}

/* Selection */
::selection {
  background: var(--primary-color);
  color: white;
}

/* Focus Styles */
button:focus-visible,
a:focus-visible {
  outline: 2px solid var(--primary-color);
  outline-offset: 2px;
}

.download {
  padding: 80px 0 60px 0;
  background: #1a1a2e;
  border-radius: 0;
  width: 100%;
  box-sizing: border-box;
}

.download-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 24px;
}

.download-content .section-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 12px;
}

.download-content .section-description {
  font-size: 1.25rem;
  color: var(--text-secondary);
  margin-bottom: 24px;
}

.download-btn {
  margin-top: 12px;
  min-width: 240px;
  font-size: 1.1rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  box-shadow: 0 4px 16px 0 rgba(139, 92, 246, 0.15);
}

.footer-card {
  width: 100%;
  max-width: none;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* --- UI Polish --- */

/* Consistent card border-radius and shadow */
.feature-card, .service-card, .footer-card {
  border-radius: 16px;
  box-shadow: 0 4px 24px 0 rgba(139,92,246,0.08);
  transition: box-shadow 0.3s, border-color 0.3s, transform 0.3s;
}

.feature-card:hover, .service-card:hover {
  box-shadow: 0 8px 32px 0 rgba(139,92,246,0.15);
  border-color: var(--primary-color);
  transform: translateY(-4px) scale(1.02);
}

/* Consistent button radius and smooth hover */
.btn {
  border-radius: 8px;
  transition: background 0.3s, color 0.3s, box-shadow 0.3s, transform 0.3s;
}

.btn:hover {
  box-shadow: 0 6px 20px 0 rgba(139,92,246,0.12);
  transform: translateY(-2px) scale(1.04);
}

/* Section padding and divider */
.features, .services, .download, .footer {
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
}

.section-divider {
  width: 100%;
  height: 40px;
  background: linear-gradient(180deg, transparent 0%, #1a1a2e 100%);
  margin-top: -40px;
  z-index: 2;
  position: relative;
}

/* Typography polish */
.section-title, .feature-title, .service-name {
  letter-spacing: -0.01em;
  line-height: 1.15;
}

.section-description, .feature-description, .service-description {
  line-height: 1.7;
  margin-bottom: 18px;
}

.feature-card, .service-card {
  margin-bottom: 0;
}

/* Mobile polish */
@media (max-width: 768px) {
  .features, .services, .download, .footer {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .feature-card, .service-card, .footer-card {
    border-radius: 12px;
  }
  .btn {
    border-radius: 6px;
  }
}

.why-choose {
  margin-bottom: 64px;
  padding-top: 64px;
}

.showcase {
  margin-bottom: 80px;
}

.free-forever-highlight {
  padding: 64px 0 32px 0;
  text-align: center;
}

.free-forever-title {
  font-size: 2.5rem;
  font-weight: 900;
  color: var(--text-primary);
  letter-spacing: -1px;
  margin: 0 auto;
  max-width: 900px;
  line-height: 1.15;
}